import { courses } from "@/lib/courses";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { notFound } from "next/navigation";
import CourseImage from "@/components/CourseImage";
import LikeButton from "@/components/LikeButton";
import PaymentButton from "@/components/PaymentButton";
import { useUser } from "@clerk/nextjs";

interface CourseDetailPageProps {
  params: { id: string };
}

export default function CourseDetailPage({ params }: CourseDetailPageProps) {
  const course = courses.find((c) => c.id === params.id);
  if (!course) return notFound();

  const user = useUser();

  return (
    <main className="max-w-xl mx-auto py-10 px-4">
      <Card>
        <CardHeader>
          <CardTitle className="text-2xl font-bold">{course.title}</CardTitle>
        </CardHeader>
        <CardContent>
          <CourseImage
            src={course.image}
            alt={course.title}
            className="w-full h-60 object-cover rounded mb-4"
          />
          <p className="text-base text-gray-700 mb-4">{course.description}</p>
          <div className="flex items-center justify-between mt-4">
            <span className="text-lg font-semibold">{course.price.toLocaleString()}원</span>
            <LikeButton courseId={course.id} />
            <Button variant="outline" asChild>
              <a href="/">목록으로</a>
            </Button>
          </div>
          {user.user && (
            <PaymentButton
              orderId={`order_${course.id}_${user.user.id}`}
              amount={course.price}
              orderName={course.title}
              customerName={user.user.fullName || user.user.username || "회원"}
            />
          )}
          {!user.user && (
            <div className="mt-4 text-center text-gray-400 text-sm">로그인 후 결제할 수 있습니다.</div>
          )}
        </CardContent>
      </Card>
    </main>
  );
} 